.backgroundBlockButton{
    width: 95%;
    height: 95%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    border-radius: 2vw;
    text-align: center;
    color: silver;
    position: relative; 
    margin: 0;
}

.backgroundBlockButton::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: 
        linear-gradient(
            180deg, 
            rgba(67, 79, 146, 0.4) 0%,    
            rgba(67, 79, 146, 0.4) 100%
        ),
        url(../Images/Textures/BackgroundButtons.jpg);
    background-size: cover;
    background-blend-mode: normal;
    border-radius: 2vw;
    opacity: 0.8;
    z-index: -1; 
    margin: 0;
}

.overlayBlockText{
